import { Observable } from "rxjs";
import { Injectable } from "@angular/core";

import { HttpClient, HttpParams } from "@angular/common/http";
import { environment } from "@env/environment";
import {
  PFAvailabilityDto,
  AvailabilityWithMatchesDto
} from "../model/availability";
import {
  ReservationDto,
  SearchParams,
  MatchWithLocationDto
} from "../model/playing-field";

const API_URL = environment.apiUrl;

@Injectable({
  providedIn: "root"
})
export class MatchService {
  constructor(private http: HttpClient) {}

  removeMatch(id) {
    return this.http.delete(`${API_URL}/matches/${id}`);
  }
  getPFAvailabilities(id): Observable<AvailabilityWithMatchesDto[]> {
    return this.http.get<AvailabilityWithMatchesDto[]>(
      `${API_URL}/worker/playingField/${id}`
    );
  }
  setPFAvailability(
    availability: PFAvailabilityDto,
    id: number
  ): Observable<any> {
    return this.http.post<Observable<any>>(
      `${API_URL}/worker/playingField/${id}/setAvailability`,
      availability
    );
  }
  deletePFAvailability(id) {
    return this.http.delete(
      `${API_URL}/worker/playingField/playingFieldAvailabilities/${id}`
    );
  }
  bookPF(id, reservation: ReservationDto): Observable<CodeDto> {
    return this.http.put<CodeDto>(`${API_URL}/matches/${id}/book`, reservation);
  }
  unbookPF(id): Observable<any> {
    return this.http.put(`${API_URL}/matches/${id}/unbook`, null);
  }

  getMatchesByLocation(
    searchParams: SearchParams,
    httpParams?: HttpParams | any
  ): Observable<any[]> {
    return this.http.post<any[]>(
      `${API_URL}/matches/getByLocation`,
      searchParams,
      { params: httpParams }
    );
  }

  getMatchesByUserId(id, httpParams?: HttpParams | any): Observable<any> {
    return this.http.get(`${API_URL}/matches/users/${id}/getAllMatches`, {
      params: httpParams
    });
  }

  getMatchesByUserIdAndReceiverNot(id, httpParams?: HttpParams | any): Observable<any> {
    return this.http.get(`${API_URL}/matches/users/${id}/getAllMatchesAndReiceverNot`, {
      params: httpParams
    });
  }
  fillCode(id) {
    return this.http.put(`${API_URL}/matches/${id}/fillCode`, null);
  }
  fillCodeByCode(code) {
    let formData = new FormData();
    formData.append("code", code);
    return this.http.put(`${API_URL}/matches/fillCode`, formData);
  }
  getMatchById(id): Observable<MatchWithLocationDto> {
    return this.http.get<MatchWithLocationDto>(`${API_URL}/matches/${id}`);
  }
}

export interface CodeDto {
  code: string;
}
