import { Observable } from "rxjs";
import { Injectable } from "@angular/core";

import { HttpClient, HttpParams } from "@angular/common/http";
import { environment } from "@env/environment";
import {
  PFAvailabilityDto,
  AvailabilityWithMatchesDto,
  InviteDto
} from "../model/availability";
import {
  ReservationDto,
  SearchParams,
  MatchWithLocationDto
} from "../model/playing-field";
import { UserUsername, UsersUsernameDto, InviteWithUserDto } from "../model";

const API_URL = environment.apiUrl;

@Injectable({
  providedIn: "root"
})
export class InviteService {
  constructor(private http: HttpClient) {}

  getInvites(httpParams?: HttpParams | any): Observable<any[]> {
    return this.http.get<any[]>(`${API_URL}/users/invites`, {
      params: httpParams
    });
  }

  sendInvite(id, matchId): Observable<number> {
    return this.http.post<number>(
      `${API_URL}/matches/${matchId}/invites/${id}`,
      null
    );
  }
  acceptInvite(id): Observable<MatchWithLocationDto> {
    return this.http.put<MatchWithLocationDto>(
      `${API_URL}/invites/${id}/accept`,
      null
    );
  }
  declineInvite(id) {
    return this.http.put(`${API_URL}/invites/${id}/decline`, null);
  }

  getMatchInvites(id): Observable<InviteWithUserDto[]> {
    return this.http.get<InviteWithUserDto[]>(
      `${API_URL}/matches/${id}/invites`
    );
  }
  getUserRequests(id): Observable<number[]> {
    return this.http.get<number[]>(`${API_URL}/users/${id}/invites`);
  }
}
