export interface UserDto {
  id?: number;
  username: string;
  banned?: boolean;
  active?: boolean;
  registered?: Date;
  lastLogin?: Date;
  playingFieldId?: number;
  avatar?: any;
  email?: string;
  phoneNumber?: string;
  lastName?: string;
  age?: number;
  firstName?: string;
  position?: string;
  city?: number;
  matchesCount?: number;
  warnings?: number;
}

export class EditUserDto {
  id: number;
  age: string;
  phoneNumber: string;
  avatar: File;
  position: string;
  city: string;
  firstName: string;
  lastName: string;
  email: string;
  constructor(
    id,
    age,
    phoneNumber,
    avatar,
    position,
    city,
    firstName,
    lastName,
    email
  ) {
    this.id = id;
    this.age = age;
    this.phoneNumber = phoneNumber;
    this.avatar = avatar;
    this.position = position;
    this.city = city;
    this.firstName = firstName;
    this.lastName = lastName;
    this.email = email;
  }
}
export class UserCredentials {
  username: string;
  password: string;
  constructor(username: string, password: string) {
    this.username = username;
    this.password = password;
  }
}

export class RegisterUserDto {
  userCredentials: UserCredentials;
  position: Position;
  constructor(userCredentials, position) {
    this.userCredentials = userCredentials;
    this.position = position;
  }
}
export enum UserRole {
  USER = "ROLE_USER",
  ADMIN = "ROLE_ADMIN",
  WORKER = "ROLE_WORKER"
}
export enum Position {
  Bramkarz = "GOALKEEPER",
  Obrońca = "DEFENDER",
  Pomocnik = "MIDFIELDER",
  Napastnik = "FORWARD"
}

export interface UserUsername {
  username: string;
}

export interface UserFriendDto {
  userDto: UserDto;
  isFriend: boolean;
  isRequestSended: boolean;
}

export interface UsersUsernameDto {
  username: string;
  id: number;
  position: string;
}
export interface InviteWithUserDto {
  id: number;
  user: UsersUsernameDto;
}

export interface NotificationDto {
id: number;
sendedDate: Date;
title: string;
senderId: number;
senderUsername: string;
entityId: number;
displayed: boolean;
notificationType: string;
accepted?: boolean;
externalId: number;
}

export enum NotificationType {
  MESSAGE, FRIENDREQUEST
}
