import { HttpParams, } from '@angular/common/http';

import { Observable } from 'rxjs';
import { Injectable } from '@angular/core';
import { UserDto} from '../model';

import {HttpClient } from '@angular/common/http';
import { environment } from '@env/environment';
import { TeamDto } from '../model/team';

const API_URL = environment.apiUrl;

@Injectable({
  providedIn: 'root'
})
export class TeamService {

  constructor(private http: HttpClient) { }

  getTeamsByMatch(id): Observable<TeamDto[]> {
    return this.http.get<TeamDto[]>(`${API_URL}/match/${id}/teams`);
  }

  joinToTeam(id) {
    return this.http.post(`${API_URL}/team/${id}/join`, null)
  }

  exitTeam(id){
    return this.http.delete(`${API_URL}/team/${id}/exit`)
  }

  swapTeam(id) {
    return this.http.put(`${API_URL}/match/${id}/swapTeam`,null)

  }

  deleteUserFromTeam(teamId, userId){
    return this.http.delete(`${API_URL}/team/${teamId}/player/${userId}`)
  }

}
