import { Observable } from "rxjs";
import { Injectable } from "@angular/core";

import { HttpClient, HttpParams } from "@angular/common/http";
import { environment } from "@env/environment";
import { MatchWithLocationDto } from "../model/playing-field";
import { NotificationDto } from "../model";

const API_URL = environment.apiUrl;

@Injectable({
  providedIn: "root"
})
export class NotificationService {
  constructor(private http: HttpClient) {}

  getNotifications(): Observable<NotificationDto[]> {
    return this.http.get<NotificationDto[]>(`${API_URL}/notifications`);
  }

  setAsDisplayed(id, accepted) {
    let formData = new FormData();
    formData.append("accepted", accepted);
    return this.http.put(
      `${API_URL}/notifications/${id}/setAsDisplayed`,
      formData
    );
  }

  setAllAsDisplayed() {
    return this.http.put(`${API_URL}/notifications/setAllAsDisplayed`, null);
  }
}
