import { Injectable } from "@angular/core";
import { Router, CanActivate, CanLoad, ActivatedRoute } from "@angular/router";
import { AuthService } from "./auth.service";
@Injectable()
export class MainAuthGuardService implements CanActivate {
  constructor(public auth: AuthService, public router: Router, private activatedRouter: ActivatedRoute) {}

  canActivate(): boolean {
    if (this.auth.isLogged() && window.location.href==="http://localhost:4200/") {
      this.router.navigate(["/users/" +localStorage.getItem('userId')]);
      return false;
    } else {
      return true;
    }
  }

}
