import { Time } from "@angular/common";

export class PlayingFieldSetupDto {
  description: string;
  teamSize: number;
  pfPhoto: File;
  name: string;
  constructor(description, teamSize, pfPhoto, name) {
    this.description = description;
    this.teamSize = teamSize;
    this.pfPhoto = pfPhoto;
    this.name = name;
  }
}

export class SearchDto {
  fromDate: Date;
  toDate: Date;
  fromTime: Time;
  toTime: Time;
  showPrivate: boolean;
  showFull: boolean
  reserved: number;
  constructor(fromDate, toDate, fromTime, toTime, showPrivate, showFull, reserved) {
    this.fromDate = fromDate;
    this.toDate = toDate;
    this.fromTime = fromTime;
    this.toTime = toTime;
    this.showPrivate = showPrivate;
    this.showFull = showFull;
    this.reserved = reserved;
  }
}

export class SearchParams {
  lat: number;
  lng: number;
  rangeInKm: number;
  searchDto: SearchDto;
  constructor(
    lat: number,
    lng: number,
    rangeInKm: number,
    searchDto: SearchDto
  ) {
    this.lat = lat;
    this.lng = lng;
    this.rangeInKm = rangeInKm;
    this.searchDto = searchDto;
  }
}
export class ReservationDto {
  phoneNumber: string;
  email: string;
  lastName: string;
  isPrivate: boolean;
  constructor(phoneNumber, email, lastName, isPrivate) {
    this.phoneNumber = phoneNumber;
    this.email = email;
    this.lastName = lastName;
    this.isPrivate = isPrivate;
  }
}

export interface MatchWithLocationDto {
  id: number;
  matchFrom: Date;
  matchTo:Date;
  address: string;
  name: string;
  lat:number;
  lng: number;
  size: number;
  maxSize: number;
  isBooked: boolean;
  isPrivate: boolean;
  ownerId: number;
  ownerUsername: string;
  isBanned: boolean;
  editable: boolean;
  code: string;
  rate?: number;
  pfId: number;
}

export interface BanDto{
  id: number;
  playingFieldId: number;
  userId: number;
  username: string
}

export interface RateDto {
 rate: number;
 numberOfVotes: number;
}
