import { HttpParams } from "@angular/common/http";

import { Observable } from "rxjs";
import { Injectable } from "@angular/core";
import {
  UserDto,
  EditUserDto,
  UserUsername,
  UserFriendDto,
  UsersUsernameDto
} from "../model";

import { HttpClient } from "@angular/common/http";
import { environment } from "@env/environment";

const API_URL = environment.apiUrl;

@Injectable({
  providedIn: "root"
})
export class UserService {
  public user: UserDto;

  constructor(private http: HttpClient) {}

  getUsersForAdmin(httpParams?: HttpParams | any): Observable<any[]> {
    return this.http.get<any[]>(`${API_URL}/users`, { params: httpParams });
  }

  getUser(id): Observable<UserDto> {
    return this.http.get<UserDto>(`${API_URL}/users/${id}`);
  }
  getUserUsername(id): Observable<UserUsername> {
    return this.http.get<UserUsername>(`${API_URL}/users/${id}/username`);
  }
  deleteUser(userId: number) {
    return this.http.delete(`${API_URL}/users/${userId}`);
  }

  banUser(id: number) {
    return this.http.put(`${API_URL}/users/${id}/ban`, this.user);
  }

  unbanUser(id: number) {
    return this.http.put<UserDto>(`${API_URL}/users/${id}/unban`, this.user);
  }

  getLoggedUser(): Observable<UserDto> {
    return this.http.get<UserDto>(`${API_URL}/users/getLoggedUser`);
  }
  getLoggedUserWithAvatar(): Observable<UserDto> {
    return this.http.get<UserDto>(`${API_URL}/users/getLoggedUser/withAvatar`);
  }
  getUsers(city, position): Observable<UserFriendDto[]> {
    let formData = new FormData();
    formData.append("city", city);
    formData.append("position", position);
    return this.http.post<UserFriendDto[]>(`${API_URL}/users`, formData);
  }
  editUser(editUser: EditUserDto) {
    let formData = new FormData();

    formData.append("age", editUser.age ? editUser.age : '');
    formData.append(
      "phoneNumber",
      editUser.phoneNumber ? editUser.phoneNumber : ''
    );
    formData.append("avatar", editUser.avatar ? editUser.avatar : null)
    formData.append("position", editUser.position ? editUser.position : "");
    formData.append("city", editUser.city ? editUser.city : "");
    formData.append("firstName", editUser.firstName ? editUser.firstName : "");
    formData.append("lastName", editUser.lastName ? editUser.lastName : "");
    formData.append("email", editUser.email ? editUser.email : "");

    return this.http.put(
      `${API_URL}/users/${editUser.id}/editProfile`,
      formData
    );
  }
  getUserByUsername(username): Observable<UserDto> {
    let formData = new FormData();
    formData.append("username", username);
    return this.http.post<UserDto>(`${API_URL}/users/byUsername`, formData);
  }
  getUsersUsername(
    matchId,
    onlyFriends: boolean
  ): Observable<UsersUsernameDto[]> {
    return this.http.post<UsersUsernameDto[]>(
      `${API_URL}/users/usernames/matches/${matchId}`,
      new OnlyFriends(onlyFriends)
    );
  }
  deleteWorker(id) {
    return this.http.put(`${API_URL}/users/${id}/dismissWorker`, null);
  }
  restoreAccount(id) {
    return this.http.put(`${API_URL}/users/${id}/restoreAccount`, null);
  }
}

export class OnlyFriends {
  onlyFriends: boolean;
  constructor(onlyFriends) {
    this.onlyFriends = onlyFriends;
  }
}
