import { Observable } from "rxjs";
import { Injectable } from "@angular/core";

import { HttpClient, HttpParams } from "@angular/common/http";
import { environment } from "@env/environment";
import { FriendsRequestDto, FriendsDto } from "../model/friends";

const API_URL = environment.apiUrl;

@Injectable({
  providedIn: "root"
})
export class FriendsService {
  constructor(private http: HttpClient) {}

  getSendedRequests(httpParams?: HttpParams | any): Observable<String[]> {
    return this.http.get<String[]>(
      `${API_URL}/friends/requests/sendedRequests`,
      { params: httpParams }
    );
  }

  getFriendRequests(httpParams?: HttpParams | any): Observable<any[]> {
    return this.http.get<any[]>(`${API_URL}/friends/requests`, {
      params: httpParams
    });
  }
  sendRequest(id) {
    return this.http.post(`${API_URL}/friends/requests/users/${id}/send`, null);
  }

  removeRequest(id) {
    return this.http.put(`${API_URL}/friends/requests/${id}/decline`, null);
  }
  acceptRequest(id): Observable<FriendsDto> {
    return this.http.put<FriendsDto>(
      `${API_URL}/friends/requests/${id}/accept`,
      null
    );
  }
  getAllFriends(id): Observable<FriendsDto[]> {
    return this.http.get<FriendsDto[]>(`${API_URL}/friends/${id}`);
  }
  deleteFriend(id) {
    return this.http.delete(`${API_URL}/friends/${id}`);
  }
  isFriend(id): Observable<boolean> {
    return this.http.get<boolean>(`${API_URL}/friends/${id}/isFriend`);
  }
}
