import { Component, OnInit, Inject } from "@angular/core";
import {
  MAT_DIALOG_DATA,
  MatDialogRef,
  MatDialog
} from "@angular/material/dialog";
import { PlayingFieldService } from "@app/shared/service/playing-field.service";
import { MatchService } from "@app/shared/service/match.service";
import { FormGroup, FormControl } from "@angular/forms";
import { ReservationDto } from "@app/shared/model/playing-field";
import { ToastrService } from "ngx-toastr";
import { CodeDialogComponent } from "../code-dialog/code-dialog.component";

@Component({
  selector: "app-reservation-dialog",
  templateUrl: "./reservation-dialog.component.html",
  styleUrls: ["./reservation-dialog.component.scss"]
})
export class ReservationDialogComponent implements OnInit {
  reservationForm: FormGroup;
  isProfilUpdated: boolean;
  constructor(
    @Inject(MAT_DIALOG_DATA)
    public data,
    private dialogRef: MatDialogRef<ReservationDialogComponent>,
    public dialog: MatDialog,
    private matchService: MatchService,
    private toastrService: ToastrService
  ) {}

  ngOnInit() {
    this.initForm();
  }

  initForm() {
    if (this.data.loggedUser) {
      this.reservationForm = new FormGroup({
        phoneNumber: new FormControl(
          this.data.loggedUser.phoneNumber
            ? this.data.loggedUser.phoneNumber
            : ""
        ),
        lastName: new FormControl(
          this.data.loggedUser.lastName ? this.data.loggedUser.lastName : ""
        ),
        email: new FormControl(
          this.data.loggedUser.email ? this.data.loggedUser.email : ""
        ),
        isPrivate: new FormControl(false)
      });
    } else {
      this.reservationForm = new FormGroup({
        phoneNumber: new FormControl(),
        lastName: new FormControl(),
        email: new FormControl(),
        isPrivate: new FormControl(false)
      });
    }
  }
  makeReservation() {
    const reservation = new ReservationDto(
      this.reservationForm.value.phoneNumber,
      this.reservationForm.value.email,
      this.reservationForm.value.lastName,
      this.reservationForm.value.isPrivate
    );
    this.matchService.bookPF(this.data.match.id, reservation).subscribe(
      val => {
        this.toastrService.success("Poprawnie zarezerwowano orlik, dziękujemy");
        this.dialog.open(CodeDialogComponent, {
          width: "500px",
          data: { code: val.code }
        });
        this.dialogRef.close({
          id: this.data.match.id,
          isBooked: true,
          isPrivate: this.reservationForm.value.isPrivate,
          code: val.code
        });
      },
      () => {
        this.toastrService.error("Nie udało się zarezerwować orliku");
        this.dialogRef.close({ id: this.data.match.id, isBooked: false });
      }
    );
  }
}
