import { Observable } from "rxjs";
import { Injectable } from "@angular/core";

import { HttpClient, HttpParams } from "@angular/common/http";
import { environment } from "@env/environment";
import { FriendsRequestDto, FriendsDto } from "../model/friends";
import { MessageDto, ConversationDto } from "../model/message";

const API_URL = environment.apiUrl;

@Injectable({
  providedIn: "root"
})
export class MessageService {
  constructor(private http: HttpClient) {}

  getMessages(id): Observable<MessageDto[]> {
    return this.http.get<MessageDto[]>(`${API_URL}/messages/${id}`);
  }

  sendMessage(id, content): Observable<MessageDto> {
    let formData = new FormData();
    formData.append("content", content);

    return this.http.post<MessageDto>(
      `${API_URL}/messages/${id}/sendMessage`,
      formData
    );
  }
  getConservations(
    httpParams?: HttpParams | any
  ): Observable<any[]> {
    return this.http.get<any[]>(`${API_URL}/messages`, {
      params: httpParams
    });
  }
}
