import { Component, OnInit } from "@angular/core";
import { FormGroup, Validators, FormControl } from "@angular/forms";
import { MatDialogRef } from "@angular/material/dialog";
import { AuthService } from "@app/core/service";
import {
  UserCredentials,
  UserRole,
  RegisterUserDto,
  Position
} from "@app/shared/model";
import { HttpResponse, HttpErrorResponse } from '@angular/common/http';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: "app-register-dialog",
  templateUrl: "./register-dialog.component.html",
  styleUrls: ["./register-dialog.component.scss"]
})
export class RegisterDialogComponent implements OnInit {
  hidingPassword = true;
  hidingPasswordConfirm = true;
  isWorker = false;
  selectedFile: File;
  public registerForm: FormGroup;
  positions = ["Bramkarz", "Obrońca", "Pomocnik", "Napastnik"];
  constructor(
    private authService: AuthService,
    public dialogRef: MatDialogRef<RegisterDialogComponent>,
    private toastr: ToastrService
  ) {}

  ngOnInit() {
    this.registerForm = new FormGroup({
      login: new FormControl(null, [
        Validators.required,
        Validators.minLength(4)
      ]),
      password: new FormControl(null, [
        Validators.required,
        Validators.minLength(6)
      ]),
      passwordConfirm: new FormControl(null, Validators.required),
      position: new FormControl(null, Validators.required)
    });
  }

  processFile(event) {
    this.selectedFile = event.target.files[0];
  }

  register() {
    this.authService
      .register(this.getRegistrationDetails())
      .subscribe(this.onSuccess, this.onFail);
  }

  private getRegistrationDetails(): RegisterUserDto {
    const formValue = this.registerForm.value;
    const userCredentials = new UserCredentials(
      formValue.login,
      formValue.password
    );
    let position;
    if (formValue.position === "Bramkarz") {
      position = Position.Bramkarz;
    } else if (formValue.position === "Obrońca") {
      position = Position.Obrońca;
    } else if (formValue.position === "Pomocnik") {
      position = Position.Pomocnik;
    } else {
      position = Position.Napastnik;
    }

    return {
      userCredentials: userCredentials,
      position: position
    };
  }

  private onFail = (error:HttpErrorResponse) => {
    if(error.status ===409) {
      this.toastr.error('Użytkownik z podaną nazwą użytkownika już istnieje')
    }
  };

  private onSuccess = () => {
    this.toastr.success('Dziękujemy za rejestrację')
    this.dialogRef.close();
  };
}
