import { Observable } from "rxjs";
import { Injectable } from "@angular/core";

import { HttpClient } from "@angular/common/http";
import { environment } from "@env/environment";
import { FriendsRequestDto, FriendsDto } from "../model/friends";
import { BanDto } from "../model/playing-field";

const API_URL = environment.apiUrl;

@Injectable({
  providedIn: "root"
})
export class BanService {
  constructor(private http: HttpClient) {}

  getBans(playingFieldId): Observable<BanDto[]> {
    return this.http.get<BanDto[]>(
      `${API_URL}/playingField/${playingFieldId}/bans`
    );
  }

  banUser(playingFieldId, userId): Observable<BanDto> {
    return this.http.post<BanDto>(
      `${API_URL}/playingField/${playingFieldId}/users/${userId}/ban`,
      null
    );
  }

  unbanUser(playingFieldId, userId) {
    return this.http.put(
      `${API_URL}/playingField/${playingFieldId}/users/${userId}/unban`,
      null
    );
  }

  isUserBanned(userId, playingFieldId): Observable<boolean> {
    return this.http.get<boolean>(
      `${API_URL}/playingField/${playingFieldId}/users/${userId}/isBanned`
    );
  }
}
